import React, { Component } from 'react';
import { withRouter, BrowserRouter as Router, Route, Link  } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';

class Allusers extends Component {

  constructor(props) {
      super(props);
      this.state = {
        allusers: []
      }
  }

  componentDidMount(){
    fetch("http://localhost:8008/api/allusers")
    .then(res => res.json())
    .then(
      (result) => {
            console.log("STATE FROM ALL USERS:-----", result)
          this.setState({
            allusers: result[0].users
          }, () => {
            console.log("STATE FROM ALL USERS:-----", this.state.allusers)
          })
      },
      (error) => {
        console.log(error)
      }
    )
  }

  render() {
    return (
      <div>
        <h3>All Users</h3> 
        <Table onRowSelection={this.handleRowSelection}
                   selectable={false}
                   multiSelectable={false}
                   deselectOnClickaway={false}>
          <TableHeader enableSelectAll={false}
                       displaySelectAll={false}
                       adjustForCheckbox={false}>
            <TableRow>
              <TableHeaderColumn>#</TableHeaderColumn>
              <TableHeaderColumn>User Name</TableHeaderColumn>
              <TableHeaderColumn>Password</TableHeaderColumn>
              <TableHeaderColumn>Name</TableHeaderColumn>
              <TableHeaderColumn>Email</TableHeaderColumn>
              <TableHeaderColumn>Contact</TableHeaderColumn>
              <TableHeaderColumn>No. of Projects</TableHeaderColumn>
              <TableHeaderColumn>actions</TableHeaderColumn>
            </TableRow>
          </TableHeader>
          <TableBody displayRowCheckbox={false} 
                     showRowHover={true}>
              {this.state.allusers.map((req, i) => UserRows(req, i) )}
          </TableBody>
        </Table>
      </div>
    );
  }
}
const propTypes = {
  className: PropTypes.string,
};

const UserRows = (obj, i) => (
  <TableRow key={obj._id}>
    <TableRowColumn>{i+1}</TableRowColumn>
    <TableRowColumn>{obj.username}</TableRowColumn>
    <TableRowColumn>{obj.password}</TableRowColumn>
    <TableRowColumn>{obj.name}</TableRowColumn>
    <TableRowColumn>{obj.email}</TableRowColumn>
    <TableRowColumn>{obj.contact}</TableRowColumn>
    <TableRowColumn>{obj.projects.length}</TableRowColumn>
    <TableHeaderColumn><Link to={'user/'+obj._id}>edit</Link></TableHeaderColumn>
  </TableRow>
)

export default withRouter(Allusers);



