import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import SelectField from 'material-ui/SelectField';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import MenuItem from 'material-ui/MenuItem';
import AddBtn from 'material-ui/svg-icons/action/done';

const styles = {
  customWidth: {
    width: '100%',
  },
};

class Adduser extends Component {

  constructor(props) {
    super(props);
    this.state = {
      projects: [],
      allprojects: [],
      username: '',
      password: '',
      name: '',
      email: '',
      contact: ''
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleInputChange = this.handleInputChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  componentDidMount() {

    fetch("http://localhost:8008/api/allprojects")
    .then(res => res.json())
    .then(
      (result) => {
            console.log("STATE FROM ALL Projects:-----", result)
          this.setState({
            allprojects: result[0].projects
          }, () => {
            console.log("STATE FROM ALL Users:-----", this.state.allprojects)
          })
      },
      (error) => {
        console.log(error)
      }
    )
    
  }

  handleChange(event, index, projects){
    event.preventDefault()

    this.setState({projects})

    console.log("select", projects)
    
  }

  handleInputChange(event){
    let name = event.target.name
    this.setState({
      [name]: event.target.value
    })
  }

  handleSubmit(e){
    e.preventDefault()
    console.log("submit", this.state)
    fetch("http://localhost:8008/api/adduser", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({name: this.state.name,
                              username: this.state.username,
                              password: this.state.password,
                              email: this.state.email,
                              contact: this.state.contact, 
                              projects: this.state.projects})
    })
    .then(res => res.json())
    .then((result)=>{
      console.log("RESULT FROM ADD USER:------", result)
      this.props.history.push({
        pathname: '/allusers'
      })
      
    }),
    (error) => {
      console.log(error)
    }
  }

  render() {

    return (
      <div>
        <h1>ADD USER</h1>
        <form onSubmit={this.handleSubmit}>
          <SelectField
            floatingLabelText="Projects"
            multiple={true}
            value={this.state.projects}
            onChange={this.handleChange}
            style={styles.customWidth}
          >
            {this.state.allprojects.map((project, index) => 
              <MenuItem key={index} value={project._id} primaryText={project.name} /> )}
          </SelectField>

          <TextField
            id="username"
            name="username"
            onChange={this.handleInputChange}
            floatingLabelText="Username"
            value={this.state.username}
            style={styles.customWidth}
            required
          />
          <TextField
            id="password"
            name="password"
            type="password"
            onChange={this.handleInputChange}
            floatingLabelText="Password"
            value={this.state.password}
            style={styles.customWidth}
            required
          />
          <TextField
            id="name"
            name="name"
            onChange={this.handleInputChange}
            floatingLabelText="Name"
            value={this.state.name}
            style={styles.customWidth}
            required
          />

          <TextField
            id="email"
            name="email"
            type=" email"
            onChange={this.handleInputChange}
            floatingLabelText="Email"
            value={this.state.email}
            style={styles.customWidth}
            required
          />

          <TextField
            id="contact"
            name="contact"
            onChange={this.handleInputChange}
            floatingLabelText="Contact"
            value={this.state.contact}
            style={styles.customWidth}
            required
          />


          <RaisedButton
            label="ADD"
            labelPosition="after"
            style={styles.customWidth}
            type="submit"
            primary={true}
            icon={<AddBtn />}
          />
        </form>
      </div>
    );
  }
}


export default withRouter(Adduser);



