import React, { Component } from 'react';
import { render } from 'react-dom';
import { BrowserRouter as Router, Route, hashHistory, Switch, Link } from "react-router-dom";
import AppTemplate from './commons/ui/components/AppTemplate';
import LoggedOutTemplate from './commons/ui/components/LoggedOutTemplate';
import AsyncComponent from './AsyncComponent';
import basePath from './commons/utils/basePath';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
// import Project from './commons/project/project_components/Project';

import "./style/app.scss";

const home = () => import(/* webpackChunkName: "home" */ './modules/home/index');
const projects = () => import(/* webpackChunkName: "projects" */ './modules/projects/index');
const project = () => import(/* webpackChunkName: "projects" */ './modules/project/index');
const user = () => import(/* webpackChunkName: "users" */ './modules/user/index');
const addproject = () => import(/* webpackChunkName: "addproject" */ './modules/addproject/index');
const login = () => import(/* webpackChunkName: "login" */ './modules/login/index');
const logout = () => import(/* webpackChunkName: "logout" */ './modules/logout/index');
const adduser = () => import(/* webpackChunkName: "adduser" */ './modules/adduser/index');
const allusers = () => import(/* webpackChunkName: "allusers" */ './modules/allusers/index');

class App extends Component {

    constructor(props) {
      super(props);
    
      this.loggedInMenu = [
        { title: 'Home', link: '', id: 0 }, 
        { title: 'Projects', link: '/projects', id: 1 }, 
        { title: 'Add Project', link: '/addproject', id: 2 }, 
        { title: 'All Users', link: '/allusers', id: 3 },
        { title: 'Add User', link: '/adduser', id: 4 },
        { title: 'Logout', link: '/logout', id: 5 }
      ];

      this.loggedOutMenu = [
        { title: 'Login', link: '/login', id: 0 }, 
      ];

      this.userId = localStorage.getItem('userId');

      this.state = { menuNode : [] };

      this.changeDetect = this.changeDetect.bind(this);

    }

    componentWillMount() {

      this.setState({ menuNode: (this.userId ? this.loggedInMenu : this.loggedOutMenu) })
    }

    changeDetect() {

      console.log("Change Detect App.js", this.state.menuNode);

      this.userId = localStorage.getItem('userId');
      this.setState({ menuNode: (this.userId ? this.loggedInMenu : this.loggedOutMenu) });
    
    }

    appTemplate() {
      
      return <div className="flex-container">
        <nav>
          { 
            this.state.menuNode.map((menu, i) => <Link key={menu.id} data-id={i} to={menu.link}> {menu.title} </Link>)
            
          }
        </nav>
        <div className="container">
          <div className="card">
            
            <Route path={basePath``} exact={true} component={() => <AsyncComponent moduleProvider={home} />} />
            <Route path={basePath`projects`} exact={true} component={() => <AsyncComponent moduleProvider={projects} />} />
            <Route path={basePath`project/:id`} component={() => <AsyncComponent moduleProvider={project} />} />
            <Route path={basePath`user/:id`} component={() => <AsyncComponent moduleProvider={user} />} />
            <Route path={basePath`addproject`} exact={true} component={() => <AsyncComponent moduleProvider={addproject} />} />
            <Route path={basePath`adduser`} exact={true} component={() => <AsyncComponent moduleProvider={adduser} />} />
            <Route path={basePath`allusers`} exact={true} component={() => <AsyncComponent moduleProvider={allusers} />} />
            <Route path={basePath`login`} exact={true} component={() => <AsyncComponent changeDetect={this.changeDetect} moduleProvider={login} />} />
            <Route path={basePath`logout`} exact={true} component={() => <AsyncComponent changeDetect={this.changeDetect} moduleProvider={logout} />} />
            
          </div>
        </div>
        <hr/>
        <footer>
          <span><b>Created by Khaleef Technologies</b></span>
          <a href="http://khaleef.com">All Rights Reserved</a>
        </footer>
      </div>
    }

  render(){
    console.log("Render App")
    return(
      <MuiThemeProvider>
            <Router>
              <AppTemplate>
                <Route path={basePath``} exact={true} component={() => <AsyncComponent moduleProvider={home} />} />
                <Route path={basePath`projects`} exact={true} component={() => <AsyncComponent moduleProvider={projects} />} />
                <Route path={basePath`project/:id`} component={() => <AsyncComponent moduleProvider={project} />} />
                <Route path={basePath`user/:id`} component={() => <AsyncComponent moduleProvider={user} />} />
                <Route path={basePath`addproject`} exact={true} component={() => <AsyncComponent moduleProvider={addproject} />} />
                <Route path={basePath`adduser`} exact={true} component={() => <AsyncComponent moduleProvider={adduser} />} />
                <Route path={basePath`allusers`} exact={true} component={() => <AsyncComponent moduleProvider={allusers} />} />
                <Route path={basePath`login`} exact={true} component={() => <AsyncComponent changeDetect={this.changeDetect} moduleProvider={login} />} />
                <Route path={basePath`logout`} exact={true} component={() => <AsyncComponent changeDetect={this.changeDetect} moduleProvider={logout} />} />
              </AppTemplate>

            </Router>

      </MuiThemeProvider>
    );
  }
}

render(<App />, document.getElementById("app"))


