import { withRouter, BrowserRouter as Router, Route, Link  } from 'react-router-dom';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import SelectField from 'material-ui/SelectField';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import MenuItem from 'material-ui/MenuItem';
import AddBtn from 'material-ui/svg-icons/action/done';

const styles = {
  customWidth: {
    width: '100%',
  },
};

class Project extends Component {

  constructor(props) {
    super(props);
    this.state = {
      id: '',
      users: [],
      allusers: [],
      name: '',
      url: ''
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleInputChange = this.handleInputChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)

  }

  componentDidMount(){
    const { match } = this.props
    // this.setState({id: match.params.id})
    console.log(match.path, match.url)

    const url = "http://localhost:8008/api/project?projectId="+match.params.id
    console.log("url", url) 
    
    fetch(url)
    .then(res => res.json())
    .then(
      (result) => {
            console.log("Single Project:-----", result)
          this.setState({
            id: result[0].project[0]._id,
            name: result[0].project[0].name,
            users: result[0].project[0].users,
            url: result[0].project[0].url
          }, () => {
            console.log("STATE FROM Project:-----", this.state)
          })
      },
      (error) => {
        console.log(error)
      }
    )

    //All users

    fetch("http://localhost:8008/api/allusers")
    .then(res => res.json())
    .then(
      (result) => {
            console.log("STATE FROM ALL USERS:-----", result)
          this.setState({
            allusers: result[0].users
          }, () => {
            console.log("STATE FROM ALL USERS:-----", this.state.allusers)
          })
      },
      (error) => {
        console.log(error)
      }
    )

  }

  handleChange(event, index, users){
    event.preventDefault()

    this.setState({users})

    console.log("select", users)
    
  }

  handleInputChange(event){
    let name = event.target.name
    this.setState({
      [name]: event.target.value
    })
  }

  handleSubmit(e){
    e.preventDefault()
    console.log("submit", this.state)
    fetch("http://localhost:8008/api/editproject", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({
        						id: this.state.id,
        						name: this.state.name,
                              	url: this.state.url, 
                              	users: this.state.users
                            })
    })
    .then(res => res.json())
    .then((result)=>{
      console.log("RESULT FROM ADD Project:------", result)
      this.props.history.push({
        pathname: '/projects'
      })
      
    }),
    (error) => {
      console.log(error)
    }
  }

  render() {
  	
    return (
      <div>
        <h1>EDIT PROJECT</h1>
        <form onSubmit={this.handleSubmit}>
          <SelectField
            floatingLabelText="Users"
            multiple={true}
            value={this.state.users}
            onChange={this.handleChange}
            style={styles.customWidth}
          >

            {this.state.allusers.map((user, index) => 
              <MenuItem key={index} value={user._id} primaryText={user.name} /> )}
            
          </SelectField>

          <TextField
            id="name"
            name="name"
            onChange={this.handleInputChange}
            floatingLabelText="Name"
            value={this.state.name}
            style={styles.customWidth}
            required
          />

          <TextField
            id="url"
            name="url"
            onChange={this.handleInputChange}
            floatingLabelText="Url"
            value={this.state.url}
            style={styles.customWidth}
            required
          />


          <RaisedButton
            label="EDIT"
            labelPosition="after"
            style={styles.customWidth}
            type="submit"
            primary={true}
            icon={<AddBtn />}
          />
        </form>
      </div>
    )
  }
}


export default withRouter(Project)




