// import React from 'react';
import React, { Component, PropTypes } from 'react';
import { Redirect  } from 'react-router';
import { withRouter, BrowserRouter as Router, Route, Link  } from 'react-router-dom';
import axios from 'axios';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
// export default withRouter(Login)

class Login extends Component {

  constructor(props) {
    super(props);
    this.state = {email: '', password:'', naviagte: false};

    this.handleEmailChange = this.handleEmailChange.bind(this);
    this.handlePasswordChange = this.handlePasswordChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

    console.log("Login Component userId", localStorage.getItem('userId'))
  }

  componentDidMount(){
    this.userId = localStorage.getItem('userId')
    console.log("LOGIN COMPONENT MOUNTED:===", this.userId)

    if (this.userId) {
      this.props.history.push({
        pathname: '/projects'
      })
    }
  }

	handleEmailChange(event) {
    this.setState({email: event.target.value});
	}
	
	handlePasswordChange(event) {
    this.setState({password: event.target.value});
	}


	handleSubmit(event) {
    event.preventDefault();

  	fetch("http://localhost:8008/api/auth/login", {
		    method: 'POST',
		    headers: {
		      'Content-Type': 'application/json',
		      'Accept': 'application/json'
		    },
		    body: JSON.stringify({username: this.state.email, password: this.state.password})
		})
		.then(res => res.json())
		.then((result)=>{
		
    	if (result.length != 0){
				console.log(result)
				
        localStorage.setItem("userId", result[0]._id)				
				// this.props.changeDetect();
        window.location.href = '/projects'
        console.log("userID", localStorage.getItem('userId'))
				
			}
		
    }),
		(error) => {
			console.log(error)
		}
	}

	render() {
    const {email, password} = this.props
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          <TextField 
            floatingLabelText="Username"
            type="text" 
            defaultValue={email} 
            ref="email" 
            onChange={this.handleEmailChange} 
          />
        </label>
        <label>
          <TextField 
            floatingLabelText="Password"
            type="password" 
            defaultValue={password} 
            ref="password" 
            onChange={this.handlePasswordChange} 
          />
        </label>
        
        <RaisedButton
          label="LOGIN"
          labelPosition="after"
          type="submit"
          primary={true}
        />
      </form>
    );
	}
}

Login.defaultProps = {
  email: null,
  password: null
}

export default withRouter(Login)




