import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import SelectField from 'material-ui/SelectField';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import MenuItem from 'material-ui/MenuItem';
import AddBtn from 'material-ui/svg-icons/action/done';

const styles = {
  customWidth: {
    width: '100%',
  },
};

class Addproject extends Component {

  constructor(props) {
    super(props);
    this.state = {
      users: [],
      allusers: [],
      name: '',
      url: ''
    }
    this.handleChange = this.handleChange.bind(this)
    this.handleInputChange = this.handleInputChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  componentDidMount(){

    fetch("http://localhost:8008/api/allusers")
    .then(res => res.json())
    .then(
      (result) => {
            console.log("STATE FROM ALL Users:-----", result)
          this.setState({
            allusers: result[0].users
          }, () => {
            console.log("STATE FROM ALL Users:-----", this.state.allusers)
          })
      },
      (error) => {
        console.log(error)
      }
    )
    
  }

  handleChange(event, index, users){
    event.preventDefault()

    this.setState({users})

    console.log("select", users)
    
  }

  handleInputChange(event){
    let name = event.target.name
    this.setState({
      [name]: event.target.value
    })
  }

  handleSubmit(e){
    e.preventDefault()
    console.log("submit", this.state)
    fetch("http://localhost:8008/api/addproject", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({name: this.state.name,
                              url: this.state.url, 
                              users: this.state.users})
    })
    .then(res => res.json())
    .then((result)=>{
      
      console.log("RESULT FROM ADD Project:------", result)
      this.props.history.push({
        pathname: '/projects'
      })
    }),
    (error) => {
      console.log(error)
    }
  }

  render() {

    return (
      <div>
        <h1>ADD PROJECT</h1>
        <form onSubmit={this.handleSubmit}>
          <SelectField
            floatingLabelText="Users"
            multiple={true}
            value={this.state.users}
            onChange={this.handleChange}
            style={styles.customWidth}
          >

            {this.state.allusers.map((user, index) => 
              <MenuItem key={index} value={user._id} primaryText={user.name} /> )}
            
          </SelectField>

          <TextField
            id="name"
            name="name"
            onChange={this.handleInputChange}
            floatingLabelText="Name"
            value={this.state.name}
            style={styles.customWidth}
            required
          />

          <TextField
            id="url"
            name="url"
            onChange={this.handleInputChange}
            floatingLabelText="Url"
            value={this.state.url}
            style={styles.customWidth}
            required
          />


          <RaisedButton
            label="ADD"
            labelPosition="after"
            style={styles.customWidth}
            type="submit"
            primary={true}
            icon={<AddBtn />}
          />
        </form>
      </div>
    );
  }
}


export default withRouter(Addproject);



