import { withRouter, BrowserRouter as Router, Route, Link  } from 'react-router-dom';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import SelectField from 'material-ui/SelectField';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import MenuItem from 'material-ui/MenuItem';
import AddBtn from 'material-ui/svg-icons/action/done';

const styles = {
  customWidth: {
    width: '100%',
  },
};

class User extends Component {

  constructor(props) {
    super(props);
    this.state = {
      id: '',
      projects: [],
      allprojects: [],
      name: '',
      email: '',
      password: '',
      username: '',
      contact: ''
    }

    this.handleChange = this.handleChange.bind(this)
    this.handleInputChange = this.handleInputChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)

  }

  componentDidMount(){
    const { match } = this.props
    // this.setState({id: match.params.id})
    console.log(match.path, match.url)

    const url = "http://localhost:8008/api/user?userId="+match.params.id
    console.log("url", url) 
    
    fetch(url)
    .then(res => res.json())
    .then(
      (result) => {
            console.log("Single User:-----", result)
          this.setState({
            id: result[0].user[0]._id,
            name: result[0].user[0].name,
            projects: result[0].user[0].projects,
            email: result[0].user[0].email,
            password: result[0].user[0].password,
            username: result[0].user[0].username,
            contact: result[0].user[0].contact
          }, () => {
            console.log("STATE FROM User:-----", this.state)
          })
      },
      (error) => {
        console.log(error)
      }
    )

    //All projects

    fetch("http://localhost:8008/api/allprojects")
    .then(res => res.json())
    .then(
      (result) => {
            console.log("STATE FROM ALL Projects:-----", result)
          this.setState({
            allprojects: result[0].projects
          }, () => {
            console.log("STATE FROM ALL Projects:-----", this.state.allprojects)
          })
      },
      (error) => {
        console.log(error)
      }
    )

  }

  handleChange(event, index, projects){
    
    event.preventDefault()

    this.setState({projects})

    console.log("select", projects)
    
  }

  handleInputChange(event){
    let name = event.target.name
    this.setState({
      [name]: event.target.value
    })
  }

  handleSubmit(e){

    e.preventDefault()
    
    console.log("submit", this.state)
    
    fetch("http://localhost:8008/api/edituser", {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify({
                  						id: this.state.id,
                  						name: this.state.name,
                              email: this.state.email, 
                              password: this.state.password, 
                              username: this.state.username, 
                            	contact: this.state.contact, 
                            	projects: this.state.projects
                            })
    })
    .then(res => res.json())
    .then((result)=>{
      console.log("RESULT FROM edit user:------", result)
      this.props.history.push({
        pathname: '/allusers'
      })
      
    }),
    (error) => {
      console.log(error)
    }
  }

  render() {
  	
    return (
      <div>
        <h1>EDIT USER</h1>
        <form onSubmit={this.handleSubmit}>
          <SelectField
            floatingLabelText="Projects"
            multiple={true}
            value={this.state.projects}
            onChange={this.handleChange}
            style={styles.customWidth}
          >

            {this.state.allprojects.map((project, index) => 
              <MenuItem key={index} value={project._id} primaryText={project.name} /> )}
            
          </SelectField>

          <TextField
            id="name"
            name="name"
            onChange={this.handleInputChange}
            floatingLabelText="Name"
            value={this.state.name}
            style={styles.customWidth}
            required
          />

          <TextField
            id="email"
            name="email"
            onChange={this.handleInputChange}
            floatingLabelText="Email"
            value={this.state.email}
            style={styles.customWidth}
            required
          />

          <TextField
            id="password"
            name="password"
            onChange={this.handleInputChange}
            floatingLabelText="Password"
            value={this.state.password}
            style={styles.customWidth}
            required
          />
          <TextField
            id="username"
            name="username"
            onChange={this.handleInputChange}
            floatingLabelText="Username"
            value={this.state.username}
            style={styles.customWidth}
            required
          />
          <TextField
            id="contact"
            name="contact"
            onChange={this.handleInputChange}
            floatingLabelText="Contact"
            value={this.state.contact}
            style={styles.customWidth}
            required
          />


          <RaisedButton
            label="EDIT"
            labelPosition="after"
            style={styles.customWidth}
            type="submit"
            primary={true}
            icon={<AddBtn />}
          />
        </form>
      </div>
    )
  }
}

export default withRouter(User)


