import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter, BrowserRouter as Router, Route, hashHistory, Switch, Link } from "react-router-dom";
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';

class Projects extends Component {

  constructor(props) {
      super(props);
      this.state = {
        allprojects: []
      }
  }

  componentDidMount(){
    fetch("http://localhost:8008/api/allprojects")
    .then(res => res.json())
    .then(
      (result) => {
            console.log("STATE FROM ALL Projects:-----", result)
          this.setState({
            allprojects: result[0].projects
          }, () => {
            console.log("STATE FROM ALL Projects:-----", this.state.allprojects)
          })
      },
      (error) => {
        console.log(error)
      }
    )
  }

  render() {
    return (
      <div>
        <h3>All Projects</h3> 
        <Table onRowSelection={this.handleRowSelection}
                   selectable={false}
                   multiSelectable={false}
                   deselectOnClickaway={false}>
          <TableHeader enableSelectAll={false}
                       displaySelectAll={false}
                       adjustForCheckbox={false}>
            <TableRow>
              <TableHeaderColumn>#</TableHeaderColumn>
              <TableHeaderColumn>Name</TableHeaderColumn>
              <TableHeaderColumn>Url</TableHeaderColumn>
              <TableHeaderColumn>No. of Users</TableHeaderColumn>
              <TableHeaderColumn>Actions</TableHeaderColumn>
            </TableRow>
          </TableHeader>
          <TableBody displayRowCheckbox={false} 
                     showRowHover={true}>
              {this.state.allprojects.map((req, i) => ProjectRows(req, i) )}
          </TableBody>
        </Table>
      </div>
    );
  }
}
const propTypes = {
  className: PropTypes.string,
};

const singleProject = (id) => {
	console.log(id)
}

const ProjectRows = (obj, i) => (


  <TableRow key={obj._id}>
    <TableRowColumn>{i+1}</TableRowColumn>
    <TableRowColumn>{obj.name}</TableRowColumn>
    <TableRowColumn>{obj.url}</TableRowColumn>
    <TableRowColumn>{obj.users.length}</TableRowColumn>
  	<TableHeaderColumn><Link to={'project/'+obj._id}>edit</Link></TableHeaderColumn>
  </TableRow>
)

export default withRouter(Projects);



